import React from 'react';
import { Parse } from 'parse';
import ParseReact from 'parse-react';
import { Link, State } from 'react-router';

import NotificationCenter from './NotificationCenter';
import Dropdown from './Dropdown';
import MenuItem from './MenuItem';
import ParseAuth from '../../support/ParseAuth';
import DeleteStoryButton from '../deleteStoryButton/DeleteStoryButton';
import UserProfile from '../../support/UserProfile';
import Analytics from '../../support/Analytics';
import UserTypeConstants from '../../constants/UserTypeConstants';

let NavigationBar = React.createClass({
  // mixins: [ State ],

  mixins: [ State, ParseReact.Mixin ],

  observe() {
    if (this.isActive('user')) {
      return {
        users: new Parse.Query(Parse.User).equalTo('objectId', this.getParams().id),
        currentUser: ParseReact.currentUser,
      };
    } else {
      return {
        currentUser: ParseReact.currentUser,
      };
    };
  },

  componentDidMount() {
    if (this.isActive('searchStories')) {
      this.props.handleChange({
        searchText: ''
      });
    }
  },

  handleBack() {
    let searchField = React.findDOMNode(this.refs.search);
    if (searchField) {
      searchField.value = '';
    };
    this.props.handleChange({
      searchText: null
    });
  },

  handleSearch() {
    this.context.router.transitionTo('searchStories');
    this.props.handleChange({
      searchText: ''
    });
  },

  handleSubmit(e) {
    e.preventDefault();

    this.searchChange(e);
  },

  searchChange(e) {
    e.preventDefault();

    let searchField = React.findDOMNode(this.refs.search);
    this.props.handleChange({
      searchText: searchField.value
    });
  },

  handleDelete(){
    this.context.router.transitionTo('stories');
  },

  render() {
    let loginStatus;

    const user = this.data.currentUser;
    if (user) {
      if (this.pendingQueries().length == 0) {
        let user_type = user.typeRaw == UserTypeConstants.STUDENT ? 'student' : 'teacher';
        Analytics.identify(user.objectId, {
          first_name: user.displayName,
          email: user.username,
          user_type: user_type
        });
      }

      loginStatus = (
        <li className='dropdown-header'>
          <h1 className='signed-in'>Signed in as</h1>
          <h1 className='username'>{UserProfile.getDisplayName(user)}</h1>
        </li>
      );
    } else {
      loginStatus = (
        <li>
          <h1 className='signed-out'>Guest</h1>
        </li>
      );
    }

    const homeLink = ParseAuth.loggedIn() ? 'stories' : 'dashboard';

    let backLink = (
      <Link to={homeLink}>
        <img src={require('../../assets/images/storyappylogo.svg')} />
      </Link>
    ), dropdownComponent, centerComponent, createStoryButton, deleteComponent;

    if (
      this.isActive('story') ||
      this.isActive('newStory') ||
      this.isActive('searchStories')
      ) {
      backLink = (
        <Link className='back-button' to='stories' onClick={this.handleBack}>
          <img src={require('../../assets/images/back-arrow.svg')} />
          Back
        </Link>
      );
    } else if (
      this.isActive('managelabels') ||
      this.isActive('user') ||
      this.isActive('userPanel') ||
      this.isActive('userStats') ||
      this.isActive('newUsers')) {
      backLink = (
        <Link
          className='back-button'
          to={this.props.previousPath || 'stories'}
          onClick={this.handleBack}
        >
          <img src={require('../../assets/images/back-arrow.svg')} />
          Back
        </Link>
      );
    };

    const searchForm = (
      <form onSubmit={this.handleSubmit}>
        <input type='search'
                placeholder='Search'
                ref='search'
                onClick={this.handleSearch}
                onChange={this.searchChange} />
      </form>
    );

    if (this.getRoutes() && this.getRoutes()[1].name === 'newStory') {
      centerComponent = 'New Story';
    } else if (this.getRoutes() && this.getRoutes()[1].name == 'managelabels') {
      centerComponent = 'Manage Labels';
    } else if (this.isActive('user')) {
      centerComponent = UserProfile.getDisplayName(this.data.users[0]);
    } else if (this.isActive('userPanel')) {
      centerComponent = 'Users Panel';
    } else if (this.isActive('newUsers')) {
      centerComponent = 'Create User Accounts';
    } else if (this.isActive('userStats')) {
      centerComponent = 'Writing Stats';
    } else {
      centerComponent = user ? searchForm : 'StoryAppy';
    };

    if (this.isActive('stories')) {
      centerComponent = (
        <div className="grid-block align-center">
          <div className="grid-block">
            <div className="grid-content">
              {searchForm}
            </div>
            <div className="medium-6 grid-block">
              <Link to='newStory' className='create-story button hollow'>
                Create Story
              </Link>
            </div>
          </div>
        </div>
      );
    };

    if (this.isActive('story')) {
      centerComponent = (
        <DeleteStoryButton storyId={this.getParams().id} onDelete={this.handleDelete}/>
      );
    };

    if (user) {
      let teacherComponents;
      if (user.typeRaw === UserTypeConstants.TEACHER) {
        teacherComponents = [(
            <MenuItem key={0}>
              <Link to='managelabels'>Labels</Link>
            </MenuItem>
          ), (
            <MenuItem key={1}>
              <Link to='userPanel'>Users</Link>
            </MenuItem>
          )
        ];
      };

      let profileComponent = (
        <Dropdown
          iconUrl={UserProfile.getSmallAvatar(user)}
          avatar
        >
          {loginStatus}
          <MenuItem>
            <Link to='bookmarkedStories'>Bookmarks</Link>
          </MenuItem>
          <MenuItem>
            <Link to='editCurrentUser'>Edit Profile</Link>
          </MenuItem>
          <MenuItem>
            <Link to='drafts'>Drafts</Link>
          </MenuItem>
          {teacherComponents}
          <MenuItem className="center">
            <div className="grid-block align-center shrink center">
              <Link to='logout' className="alert">Logout</Link>
            </div>
          </MenuItem>
        </Dropdown>
      );

      dropdownComponent = (
        <div className='grid-block'>
          <div className='grid-block'>
            <NotificationCenter />
          </div>
          <div className='grid-block'>
            {profileComponent}
          </div>
        </div>
      );
    } else {
      dropdownComponent = (
        <Dropdown
          iconUrl={UserProfile.getSmallAvatar(user)}
          avatar
        >
          <MenuItem>
            <Link to='login'>Login</Link>
          </MenuItem>
        </Dropdown>
      );
    };

    return (
      <div className='title-bar'>
        <div className='center title'>
          {centerComponent}
        </div>
        <span className='left'>
          {backLink}
        </span>
        <div className='right'>
          {dropdownComponent}
        </div>
      </div>
    );
  }

});

export default NavigationBar;



