import React from 'react';
import moment from 'moment';
import { Parse } from 'parse';
import ParseReact from 'parse-react';
import {Line} from 'react-chartjs';
import LineChart from './LineChart';
import classNames from 'classnames';
import UserProfile from '../../support/UserProfile';
import UserTypeConstants from '../../constants/UserTypeConstants';

const ParseComponent = ParseReact.Component(React);

export default class UserStats extends ParseComponent {
  constructor(props) {
    super(props);

    this.state = {
      activeButton: 'week',
    };

    this.handlePrint = this.handlePrint.bind(this);
  }

  observe(props, state) {
    return {
      users: new Parse.Query(Parse.User).equalTo('objectId', props.params.id),
    };
  }

  handlePrint() {
    window.print();
  }

  handleClickType(type) {
    this.setState({activeButton: type});
  }

  render() {
    const clarityData = {
      labels: ['January', 'February', 'March', 'April', 'May', 'June', 'July'],
      datasets: [
        {
            label: 'My First dataset',
            pointColor: 'rgba(220,220,220,1)',
            pointStrokeColor: '#fff',
            pointHighlightFill: '#fff',
            pointHighlightStroke: 'rgba(220,220,220,1)',
            data: [65, 59, 80, 81, 56, 55, 40]
        },
      ]
    };

    const grammarData = {
      labels: ['January', 'February', 'March', 'April', 'May', 'June', 'July'],
      datasets: [
        {
            label: 'My First dataset',
            pointColor: 'rgba(220,220,220,1)',
            pointStrokeColor: '#fff',
            pointHighlightFill: '#fff',
            pointHighlightStroke: 'rgba(220,220,220,1)',
            data: [40, 68, 30, 21, 29, 51, 18]
        },
      ]
    };

    const spellingData = {
      labels: ['January', 'February', 'March', 'April', 'May', 'June', 'July'],
      datasets: [
        {
            label: 'My First dataset',
            pointColor: 'rgba(220,220,220,1)',
            pointStrokeColor: '#fff',
            pointHighlightFill: '#fff',
            pointHighlightStroke: 'rgba(220,220,220,1)',
            data: [12, 23, 15, 41, 51, 21, 61]
        },
      ]
    };

    const user = this.data.users && this.data.users[0];

    return (
      <div className='user-stats grid-block vertical print'>
        <div className="grid-block align-center">
          <div className="small-20 grid-block profile">
            <div className="grid-block small-8">
              <img className="avatar" src={require('../../assets/images/avatar.png')}/>
              <div className="grid-block v-align content">
                <div className="grid-block vertical">
                  <div>
                    {user && UserProfile.getDisplayName(user)}
                  </div>
                  <div>
                    {user && user.typeRaw === UserTypeConstants.STUDENT ? 'Student' : 'Teacher'}
                  </div>
                </div>
              </div>
            </div>
            <div className="grid-block align-right">
              <div className="grid-block v-align small-6">
                <a
                  className={classNames({'active': this.state.activeButton === 'year'})}
                  onClick={this.handleClickType.bind(this, 'year')}
                >
                  Year
                </a>
              </div>
              <div className="grid-block v-align small-6">
                <a
                  className={classNames({'active': this.state.activeButton === 'month'})}
                  onClick={this.handleClickType.bind(this, 'month')}
                >
                  Month
                </a>
              </div>
              <div className="grid-block v-align small-6">
                <a
                  className={classNames({'active': this.state.activeButton === 'week'})}
                  onClick={this.handleClickType.bind(this, 'week')}
                >
                  Week
                </a>
              </div>
              <div className="grid-block v-align small-6 shrink noprint">
                <a className="text-right" onClick={this.handlePrint}>
                  <img src={require('../../assets/images/print.png')} alt="print"/>
                </a>
              </div>
            </div>
          </div>
        </div>
        <div className='grid-block align-center'>
          <div className="small-20 grid-block vertical">
            <div className='grid-block card card-block vertical metrics'>
              <div className="grid-block chart-header">
                <div className="v-align chart-name">
                  Writing Metrics
                </div>
                <div className="grid-block v-align align-right chart-date">
                  {`as of ${moment().format('MMM Do YYYY')}`}
                </div>
              </div>
              <div className="grid-block">
                <div className="grid-block vertical border-right stats">
                  <div className="text-center stat">
                    60
                  </div>
                  <div className="text-center">
                    Submissions Written
                  </div>
                </div>
                <div className="grid-block vertical stats">
                  <div className="text-center stat">12</div>
                  <div className="text-center">Stories Created</div>
                </div>
              </div>
            </div>
            <div className='grid-block card card-block vertical clarity'>
              <div className="grid-block chart-header">
                <div className="v-align chart-name">
                  Clarity
                </div>
                <div className="grid-block v-align align-right chart-date">
                  {`as of ${moment().format('MMM Do YYYY')}`}
                </div>
              </div>
              <div className="grid-block chart-body">
                <LineChart
                  startGradientColor='rgba(239, 222, 131, 1)'
                  endGradientColor='rgba(239, 183, 25, 1)'
                  data={clarityData}
                />
              </div>
            </div>
            <div className='grid-block card card-block vertical grammar-mistakes'>
              <div className="grid-block chart-header">
                <div className="v-align chart-name">
                  Grammar Mistakes
                </div>
                <div className="grid-block v-align align-right chart-date">
                  {`as of ${moment().format('MMM Do YYYY')}`}
                </div>
              </div>
              <div className="grid-block chart-body">
                <LineChart
                  startGradientColor='rgba(255, 177, 48, 1)'
                  endGradientColor='rgba(218, 55, 34, 1)'
                  data={grammarData}
                />
              </div>
            </div>
            <div className='grid-block card card-block vertical spelling'>
              <div className="grid-block chart-header">
                <div className="v-align chart-name">
                  Spelling Mistakes
                </div>
                <div className="grid-block v-align align-right chart-date">
                  {`as of ${moment().format('MMM Do YYYY')}`}
                </div>
              </div>
              <div className="grid-block chart-body">
                <LineChart
                  startGradientColor='rgba(95, 151, 255, 1)'
                  endGradientColor='rgba(0, 117, 206, 1)'
                  data={spellingData}
                />
              </div>
            </div>
          </div>
        </div>
      </div>
    );
  }
}



