import React from 'react';
import { Parse } from 'parse';
import ParseAuth from '../support/ParseAuth';
import UserTypeConstants from '../constants/UserTypeConstants';

function teacherAuth (Component) {
  return class TeacherAuthenticated extends React.Component {

    static willTransitionTo(transition) {
      const user = Parse.User.current();
      if (!user || (user && user.get('typeRaw') !== UserTypeConstants.TEACHER)) {
        transition.redirect('/stories', {}, {'nextPath': transition.path});
      };
    }

    render() {
      return <Component {...this.props} {...this.state}/>;
    }
  };
}

export { teacherAuth };


