import './assets/stylesheets/app';
import './assets/stylesheets/main';

import { Parse } from 'parse';
import React from 'react';
import Router from 'react-router';

import Analytics from './support/Analytics';
import secrets from '../config/secrets';
import Routes from './Routes';

Parse.initialize(secrets.parse.applicationId, secrets.parse.javascriptKey);
Analytics.init();

let previousPath = null;

Router.run(Routes, Router.HistoryLocation, (Handler, state) => {
  Analytics.page('PageView', {
    path: state.pathname
  });
  React.render(<Handler previousPath={previousPath}/>, document.body);
  previousPath = state.path;
});


