import React from 'react';
import { Route, DefaultRoute, NotFoundRoute } from 'react-router';
import App from './components/layouts/App';
import Home from './components/main/Home';
import Admin from './components/admin/Admin';
import StoryList from './components/stories/StoryList';
import Story from './components/stories/Story';
import ResetPassword from './components/sessions/ResetPassword';
import Login from './components/sessions/Login';
import Logout from './components/sessions/Logout';
import Edit from './components/users/Edit';
import NewStory from './components/stories/NewStory';
import User from './components/users/User';
import ManageLabels from './components/labels/ManageLabels';
import NotFound from './components/notFound/NotFound';
import UserPanel from './components/admin/UserPanel';
import NewUsers from './components/admin/NewUsers';
import UserStats from './components/users/UserStats';
import Drafts from './components/stories/Drafts';

module.exports = (
  <Route name='app' path='/' handler={App}>
    <DefaultRoute name='dashboard' handler={Home} />
    <Route path='/admin' handler={Admin} />
    <Route name='userPanel' path='/admin/users' handler={UserPanel} />
    <Route name='newUsers' path='/admin/users/new' handler={NewUsers} />
    <Route name='userStats' path='/users/:id/stats' handler={UserStats} />
    <Route name='stories' path='/stories' handler={StoryList}>
      <Route name='bookmarkedStories' path='/stories/bookmarks' />
      <Route name='searchStories' path='/stories/search' />
    </Route>
    <Route name='drafts' path='/drafts' handler={Drafts} />
    <Route name='newStory' path='/stories/new' handler={NewStory} />
    <Route name='story' path='/stories/:id' handler={Story} />
    <Route name='resetPassword' path='/forgot' handler={ResetPassword} />
    <Route name='login' handler={Login} />
    <Route name='logout' handler={Logout} />
    <Route name='editCurrentUser' path='/me/edit' handler={Edit} />
    <Route name='user' path='/users/:id' handler={User} />
    <Route name='managelabels' path='/manage/labels' handler={ManageLabels} />
    <NotFoundRoute name='NotFound' handler={NotFound} />
  </Route>
);



